/* 文章的标签 */
  .post-tags a {
    position: relative;
    &::before, &::after {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      height: 2px;
      background-color: #fc2f70;
        transform: scaleX(0);
      transition: transform 0.5s ease;
    }
    
    &::before {
      top: 0;
      transform-origin: center right;
    }
    
    &:hover::before {
      transform-origin: center left;
      transform: scaleX(1);
    }
    
    &::after {
      bottom: 0;
      transform-origin: center left;
    }
    
    &:hover::after {
      transform-origin: center right;
      transform: scaleX(1);
    }
    
  }


